import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import Container from '../../../atoms/Container';
import { Input, InputContainer, ViewTitle } from '../../../atoms';


const KiCalc = () => {
    const [t850, setT850] = useState(0);
    const [td850, setTd850] = useState(0);
    const [deltatd700, setDeltatd700] = useState(0);
    const [t500, setT500] = useState(0);

    let ki
    let result
    const calcKi = () => {
        let p

        ki = Number(t850) - Number(t500) + Number(td850) - Number(deltatd700)
        if (ki < 20) {
            result = ' '
        } else if (ki == 20 || ki == 25 || (ki > 20 && ki < 25)) {
            result = '  ,    10%  ,    '
        } else if (ki == 30 || (ki > 25 && ki < 30)) {
            result = ' ,    10-20%  ,    '
        } else if (ki == 35 || (ki > 30 && ki < 30)) {
            result = ' ,  20-50%  '
        } else if (ki == 40 || (ki > 35 && ki < 40)) {
            result = ' ,  50-70% '
        } else if (ki > 40) {
            result = ' ,     70% '
        }

        if (ki < 20) {
            p = ''
        } else if (ki == 20 || ki == 22 || (ki > 20 && ki > 22)) {
            p = ',   50%,  '
        } else if (ki == 25 || (ki > 22 && ki < 25)) {
            p = ',   60%,  '
        } else if (ki == 28 || (ki > 25 && ki < 28)) {
            p = ',   75%,   '
        } else if (ki == 31 || (ki > 28 && ki < 31)) {
            p = ',   80%,   '
        } else if (ki == 34 || (ki > 31 && ki < 34)) {
            p = ',   90%, '
        } else if (ki == 37 || (ki > 34 && ki < 37)) {
            p = ',   95%,  '
        } else if (ki > 37) {
            p = ',   100%,  '
        }

        return `Ki = ${ki} ${p}, ${result}`
    }
    let res = calcKi()

    return (
        <Container column justify='flex-start' align='flex-start' m='20px'>
            <Container column justyfy='start' align='start'>

                <Container column justyfy='start' align='start'>
                    <ViewTitle>
                            Ki,  
                    </ViewTitle>
                    <TheoryText>
                         Ki     ,     ,        .
                        Ki      ,       .
                        Ki=T850-T500+Td850-Td700.
                        Ki   ,
                        T850       850 ,
                        T500     500 ,
                        Td850      850 ,
                        Td700     (T-Td)   700 .

                        Ki          .         ,    .
                    </TheoryText>
                </Container>
                <Container>
                    <Container>
                        <InputContainer>
                            <Input
                                onChange={(e) => setT850(e.target.value)}
                                value={t850}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -    850 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChange={(e) => setTd850(e.target.value)}
                                value={td850}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -     850 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChange={(e) => setDeltatd700(e.target.value)}
                                value={deltatd700}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -      700 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChange={(e) => setT500(e.target.value)}
                                value={t500}
                                type='number'
                            />
                            <Container>
                                <p>
                                       500 
                                </p>
                            </Container>
                        </InputContainer>
                    </Container>
                </Container>
            </Container>
            <h3 >: {res}</h3>
        </Container>
    );
};

export default KiCalc;

const TheoryText = styled.span`
    margin: 20px;
    font-size: 16px;
    font-family: 'Roboto Thin'
`