import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import Container from '../../../atoms/Container';
import { Input, InputContainer, ViewTitle } from '../../../atoms';


const FateevCalc = () => {
    const [t850, setT850] = useState(0);
    const [deltatd850, setDeltatd850] = useState(0);
    const [deltatd700, setDeltatd700] = useState(0);
    const [t500, setT500] = useState(0);
    const [deltatd500, setDeltatd500] = useState(0);
    const [deltatd600, setDeltatd600] = useState(0);

    let a
    let result
    const calcFateev = () => {
        a = Number(t850) - Number(t500) - (Number(deltatd850) + Number(deltatd700) + Number(deltatd600) + Number(deltatd500))
        a > 0 || a == 0 ? result = '    ' : result = '     '
        return `A = ${a}, ${result}`
    }
    let res = calcFateev()

    return (
        <Container column justify='flex-start' align='flex-start' m='20px'>
            <Container column justyfy='start' align='start'>

                <Container column justyfy='start' align='start'>
                    <ViewTitle>
                            
                    </ViewTitle>
                    <TheoryText>
                          , ..        ,       .
                         .. 
                         = 850 500  (D850+D700+D600+D500).      .    ,    0,      .
                    </TheoryText>
                </Container>
                <Container>
                    <Container>
                        <InputContainer>
                            <Input
                                onChange={(e) => setT850(e.target.value)}
                                value={t850}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -    850 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChangeText={(e) => setT500(e.target.value)}
                                value={t500}
                                type='number'

                            />
                            <Container>
                                <p>
                                    -    500 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChangeText={(e) => setDeltatd850(e.target.value)}
                                value={deltatd850}
                                type='number'

                            />
                            <Container>
                                <p>
                                    -      850 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChangeText={(e) => setDeltatd700(e.target.value)}
                                value={deltatd700}
                                type='number'

                            />
                            <Container>
                                <p>
                                    -      700 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChangeText={(e) => setDeltatd500(e.target.value)}
                                value={deltatd500}
                                type='number'

                            />
                            <Container>
                                <p>
                                    -      500 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChangeText={(e) => setDeltatd600(e.target.value)}
                                value={deltatd600}
                                type='number'

                            />
                            <Container>
                                <p>
                                    -      600 
                                </p>
                            </Container>
                        </InputContainer>
                    </Container>
                </Container>
            </Container>
            <h3 >: {res}</h3>
        </Container>
    );
};

export default FateevCalc;

const TheoryText = styled.span`
    margin: 20px;
    font-size: 16px;
    font-family: 'Roboto Thin'
`