import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import Container from '../../../atoms/Container';
import { Input, InputContainer, ViewTitle } from '../../../atoms';


const SweatCalc = () => {
    const [t850, setT850] = useState(0);
    const [td850, setTd850] = useState(0);
    const [d850, setD850] = useState(0);
    const [f850, setF850] = useState(0);
    const [t500, setT500] = useState(0);
    const [f500, setF500] = useState(0);
    const [d500, setD500] = useState(0);

    let s
    let result

    const calcSweat = () => {
        const vt = Number(t850) - Number(t500)
        const ct = Number(td850) - Number(t500)
        const tt = Number(ct) + Number(vt)
        const a = (d850 == 130 || d850 == 250 || d850 > 130 || d850 < 250) && (d500 == 210 || d500 == 310 || d500 > 210 && d500 < 310) && ((d500 - d850) > 0 || (d500 - d850) == 0) && (f850 < 7 || f850 == 7) && (f500 < 7 || f500 == 7) ? 0.2 : 0
        s = ((12 * Number(td850)) + (20 * (tt - 49)) + (3.888 * Number(f850)) + (1.944 * f500) + (125 * Math.sin((Number(d500) - Number(d850)) + a)))
        if (s < 250) {
            result = '     '
        } else if (s > 250 && s < 350 || s == 250 || s == 350) {
            result = '    ,   '
        } else if (s > 350 && s < 500 || s == 500) {
            result = '    ,   '
        } else if (s > 500) {
            result = '    ,   '
        }



        return `
            VT = ${vt}, 
            CT = ${ct}, 
            TT=${tt} 
            SWEAT = ${s}, 
${result}`
    }
    let res = calcSweat()

    return (
        <Container column justify='flex-start' align='flex-start' m='20px'>
            <Container column justyfy='start' align='start'>

                <Container column justyfy='start' align='start'>
                    <ViewTitle>
                            SWEAT
                    </ViewTitle>
                    <TheoryText>
                        SWEAT  Severe Weather ThrEAT 

                        CT - ross Totals 
                        CT = Td850 - T500
                        Td850      850 
                        T500     500 

                        VT  Vertical Totals 
                        VT = T850 - T500
                        T850       850 ,
                        T500     500 .
                        VT  28 -      ,    .

                        TT  Total Totals 
                        TT = VT + CT
                        CT  ross Totals ,
                        VT  Vertical Totals .

                           
                                  ,    ,       ,    .
                        SWEAT=12Td850+20(TT-49)+3.888F850+1.944F500+ (125[sin(D500 - D850)+0.2])
                        Td850      850  (  ),
                        TT  Total Totals ,
                        F850     850  (/),
                        F500     500  (/),
                        D500  D850      . ( )
                             0,  TT  49;
                              ,       :
                        - D850    130  250 ;
                        - D500    210  310 ;
                        -     (D500 - D850) ;
                        - F850  F500    7 /.
                    </TheoryText>
                </Container>
                <Container>
                    <Container>
                        <InputContainer>
                            <Input
                                onChange={(e) => setT850(e.target.value)}
                                value={t850}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -    850 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChange={(e) => setTd850(e.target.value)}
                                value={td850}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -     850 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChange={(e) => setD850(e.target.value)}
                                value={d850}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -     850  ( )
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChange={(e) => setF850(e.target.value)}
                                value={f850}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -     850  ( /)
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChange={(e) => setT500(e.target.value)}
                                value={t500}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -    500 
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChange={(e) => setD500(e.target.value)}
                                value={d500}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -     500  ( )
                                </p>
                            </Container>
                        </InputContainer>
                        <InputContainer>
                            <Input
                                onChange={(e) => setF500(e.target.value)}
                                value={f500}
                                type='number'
                            />
                            <Container>
                                <p>
                                    -     500  ( /)
                                </p>
                            </Container>
                        </InputContainer>
                    </Container>
                </Container>
            </Container>
            <h3 >: {res}</h3>
        </Container>
    );
};

export default SweatCalc;

const TheoryText = styled.span`
    margin: 20px;
    font-size: 16px;
    font-family: 'Roboto Thin'
`