import React, { useState } from 'react';
import styled from 'styled-components';
import Container from '../../../../atoms/Container';
import { Button, Input, InputContainer, ViewTitle } from '../../../../atoms';

const PreasureCalc = () => {
    const [t, setT] = useState(0);
    const [mm, setMm] = useState(true);
    const [preasure, setPreasure] = useState(0);
    const [height, setHeight] = useState(0);
    const [aimHeight, setAimHeight] = useState(0);
    const [aimHeight2, setAimHeight2] = useState(0);
    const [aimHeight3, setAimHeight3] = useState(0);

    let q; //  
    let p;
    let pSea; //    
    let pAim; // 
    let pAim2; // 
    let pAim3; // 

    const calcPreasure = () => {
        p = mm ? preasure * 0.750062 : preasure;
        q = (8000 / Number(preasure)) * (1 + (1 / 273 * t));
        pSea = (Number(preasure) + (Number(height) / Number(q)));
        pAim = (Number(pSea) - (Number(aimHeight) / Number(q)));
        pAim2 = (Number(pSea) - (Number(aimHeight2) / Number(q)));
        pAim3 = (Number(pSea) - (Number(aimHeight3) / Number(q)));

        return (
            <ResultContainer column >
                <p>
                        = ({mm ? `${Number(preasure).toFixed(2)} ...` : `${Number(preasure).toFixed(2)} `}) ({mm ? `${(preasure / 0.750062).toFixed(2)} ` : `${(preasure * 0.750062).toFixed(2)} ...`})
                </p>
                <p>
                    {isFinite(q) ? `  = ${(q).toFixed(2)} /${mm ? '...' : ''} (${(mm ? (q * 0.750062) : q / 0.750062).toFixed(2)} /${mm ? '' : '...'})` : `  `}
                </p>
                <p>
                        = {(pSea).toFixed(2)} {mm ? '...' : ''} ({(mm ? (pSea / 0.750062) : pSea * 0.750062).toFixed(2)} {mm ? '' : '...'})
                </p>
                <p>
                       {aimHeight}  = {(pAim).toFixed(2)} {mm ? '...' : ''} ({(mm ? (pAim / 0.750062) : (pAim * 0.750062)).toFixed(2)} {mm ? '' : '...'})
                </p>
                <p>
                       {aimHeight2}  = {(pAim2).toFixed(2)} {mm ? '...' : ''} ({(mm ? (pAim2 / 0.750062) : (pAim2 * 0.750062)).toFixed(2)} {mm ? '' : '...'})
                </p>
                <p>
                       {aimHeight3}  = {(pAim3).toFixed(2)} {mm ? '...' : ''} ({(mm ? (pAim3 / 0.750062) : (pAim3 * 0.750062)).toFixed(2)} {mm ? '' : '...'})
                </p>
            </ResultContainer>
        );
    };

    return (
        <Container justify='flex-start' align='flex-start' m='20px'>
            <Container column justyfy='start' align='start'>
                <ViewTitle>
                      ,         
                </ViewTitle>
                <TheoryText>
                    1 ... = 0.750062 
                </TheoryText>
                <TheoryText>
                      = 8000/p * (1 + (1/273 * T))
                </TheoryText>
                <TheoryText>
                    (          , , 2009 )
                </TheoryText>
            </Container>
            <Container column justify='flex-start' align='flex-start'>
                <Button onClick={() => setMm(!mm)}>
                    <p>{mm ? '  ' : '  ...'}</p>
                </Button>
                <InputContainer>
                    <Input
                        onChange={(e) => setT(e.target.value)}
                        value={t}
                        type="number"
                    />
                    <Container justify='flex-start' align='flex-start'>
                        <p>-    </p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setPreasure(e.target.value)}
                        value={preasure}
                        type="number"
                    />
                    <Container>
                        <p>-     ({mm ? '...' : ''})</p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setHeight(e.target.value)}
                        value={height}
                        type="number"
                    />
                    <Container>
                        <p>-   </p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setAimHeight(e.target.value)}
                        value={aimHeight}
                        type="number"
                    />
                    <Container>
                        <p>- ,        </p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setAimHeight2(e.target.value)}
                        value={aimHeight2}
                        type="number"
                    />
                    <Container>
                        <p>- ,        </p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setAimHeight3(e.target.value)}
                        value={aimHeight3}
                        type="number"
                    />
                    <Container>
                        <p>- ,        </p>
                    </Container>
                </InputContainer>
            </Container>
            <h2>:</h2>
            {calcPreasure()}
        </Container>
    );
};

export default PreasureCalc;

const TheoryText = styled.span`
    margin: 20px;
    font-size: 16px;
    font-family: 'Roboto Thin';
`

const ResultContainer = styled(Container)`
    margin: 0;
    padding-left: 20px;
    justify-content: start;
    align-items: start;
`