import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import Container from '../../../atoms/Container';
import { Input, InputContainer, ViewTitle } from '../../../atoms';


const KpoCalc = () => {
    const [prev, setPrev] = useState(0);
    const [wind, setWind] = useState(0);
    const [kv, setKv] = useState(0);
    const [t, setT] = useState(0);
    const [td, setTd] = useState(0);

    useEffect(() => {
        if (wind == 0) {
            setKv(1.00)
        } else if (wind == 1) {
            setKv(1.02)
        } else if (wind == 2) {
            setKv(1.04)
        } else if (wind == 3) {
            setKv(1.07)
        } else if (wind == 4) {
            setKv(1.11)
        } else if (wind == 5) {
            setKv(1.16)
        } else if (wind == 6) {
            setKv(1.22)
        } else if (wind == 7) {
            setKv(1.28)
        } else if (wind == 8) {
            setKv(1.32)
        } else if (wind == 9) {
            setKv(1.36)
        } else if (wind == 10) {
            setKv(1.39)
        } else if (wind == 11) {
            setKv(1.41)
        } else if (wind == 12) {
            setKv(1.43)
        } else if (wind == 13) {
            setKv(1.45)
        } else if (wind == 14) {
            setKv(1.46)
        } else if (wind == 15) {
            setKv(1.47)
        } else if (wind == 16) {
            setKv(1.48)
        } else if (wind > 16) {
            setKv(1.48 + ((wind - 16) * 0.01))
        }
    }, [wind]);
    let kpo

    const calcKpo = () => {
        kpo = Number(prev) + Number(kv) * Number(t) * (Number(t) - Number(td))
        return `Kv=${kv.toFixed(2)},
            = ${(kpo - prev).toFixed(0)}
          = ${kpo.toFixed(0)}`
    }
    let res = wind >= 0 ? calcKpo() : '   '

    return (
        <Container column justify='flex-start' align='flex-start' m='20px'>
            <Container column justyfy='start' align='start'>
                {/* <CloudImage src={kpo3} />
                <CloudImage src={kpo1} />
                <CloudImage src={kpo2} /> */}
                <Container column justyfy='start' align='start'>
                    <ViewTitle>
                          
                    </ViewTitle>
                    <TheoryText>
                                :
                        -  ,   ( ),   ( /)  13              (15 );
                        -       ( ).


                            ()     ,    (v),    (           (t).           . (n).       .
                           v,           (V -  , /)
                    </TheoryText>
                </Container>
                <InputContainer>
                    <Input
                        onChange={e => setPrev(e.target.value)}
                        value={prev}
                        type="number"
                    />
                    <Container>
                        <p>
                            -   
                        </p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setWind(e.target.value)}
                        value={wind}
                        type="number"
                    />
                    <Container>
                        <p>
                            -   (/)
                        </p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setT(e.target.value)}
                        value={t}
                        type='number'
                    />
                    <Container>
                        <p>
                            -  ()
                        </p>
                    </Container>
                </InputContainer>
                <InputContainer>
                    <Input
                        onChange={(e) => setTd(e.target.value)}
                        value={td}
                        type='number'
                    />
                    <Container>
                        <p>
                            -   ()
                        </p>
                    </Container>
                </InputContainer>

            </Container>
            <h3 >: {res}</h3>
        </Container>
    );



    // return (
    //     <Container justify='flex-start' align='flex-start' m='20px'>
    //         <Container column justyfy='start' align='start'>
    //             <h2>
    //                   
    //             </h2>
    //             <TheoryText>
    //                         :
    //                 -  ,   ( ),   ( /)  13              (15 );
    //                 -       ( ).


    //                     ()     ,    (v),    (           (t).           . (n).       .
    //                    v,           (V -  , /)
    //             </TheoryText>
    //         </Container>
    //         <Container column justify='flex-start' align='flex-start'>
    //             <Button onClick={() => setMm(!mm)}>
    //                 <p>{mm ? '  ' : '  ...'}</p>
    //             </Button>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setT(e.target.value)}
    //                     value={t}
    //                     type="number"
    //                 />
    //                 <Container justify='flex-start' align='flex-start'>
    //                     <p>-    </p>
    //                 </Container>
    //             </Container>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setPreasure(e.target.value)}
    //                     value={preasure}
    //                     type="number"
    //                 />
    //                 <Container>
    //                     <p>-     ({mm ? '...' : ''})</p>
    //                 </Container>
    //             </Container>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setHeight(e.target.value)}
    //                     value={height}
    //                     type="number"
    //                 />
    //                 <Container>
    //                     <p>-   </p>
    //                 </Container>
    //             </Container>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setAimHeight(e.target.value)}
    //                     value={aimHeight}
    //                     type="number"
    //                 />
    //                 <Container>
    //                     <p>- ,        </p>
    //                 </Container>
    //             </Container>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setAimHeight2(e.target.value)}
    //                     value={aimHeight2}
    //                     type="number"
    //                 />
    //                 <Container>
    //                     <p>- ,        </p>
    //                 </Container>
    //             </Container>
    //             <Container>
    //                 <Input
    //                     onChange={(e) => setAimHeight3(e.target.value)}
    //                     value={aimHeight3}
    //                     type="number"
    //                 />
    //                 <Container>
    //                     <p>- ,        </p>
    //                 </Container>
    //             </Container>
    //         </Container>
    //         <h2>:</h2>
    //         {calcPreasure()}
    //     </Container>
    // );
};

